.rc-pagination {
  font-size: 12px;
  font-family: Arial;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  padding: 0
}

.rc-pagination>li {
  list-style: none
}

.rc-pagination-total-text {
  float: left;
  height: 30px;
  line-height: 30px;
  list-style: none;
  padding: 0;
  margin: 0 8px 0 0
}

.rc-pagination:after {
  content: " ";
  display: block;
  height: 0;
  clear: both;
  overflow: hidden;
  visibility: hidden
}

.rc-pagination-item {
  cursor: pointer;
  border-radius: 6px;
  min-width: 28px;
  height: 28px;
  line-height: 28px;
  text-align: center;
  list-style: none;
  float: left;
  border: 1px solid #d9d9d9;
  background-color: #fff;
  margin-right: 8px
}

.rc-pagination-item a {
  text-decoration: none;
  color: #666
}

.rc-pagination-item:hover {
  border-color: #2db7f5
}

.rc-pagination-item:hover a {
  color: #2db7f5
}

.rc-pagination-item-disabled {
  cursor: not-allowed
}

.rc-pagination-item-disabled:hover {
  border-color: #d9d9d9
}

.rc-pagination-item-disabled:hover a {
  color: #d9d9d9
}

.rc-pagination-item-active {
  background-color: #2db7f5;
  border-color: #2db7f5
}

.rc-pagination-item-active:hover a,.rc-pagination-item-active a {
  color: #fff
}

.rc-pagination-jump-next:after,.rc-pagination-jump-prev:after {
  content: "\2022\2022\2022";
  display: block;
  letter-spacing: 2px;
  color: #ccc;
  font-size: 12px;
  margin-top: 1px
}

.rc-pagination-jump-next:hover:after,.rc-pagination-jump-prev:hover:after {
  color: #2db7f5
}

.rc-pagination-jump-prev:hover:after {
  content: "\AB"
}

.rc-pagination-jump-next:hover:after {
  content: "\BB"
}

.rc-pagination-jump-next-custom-icon,.rc-pagination-jump-prev-custom-icon {
  position: relative
}

.rc-pagination-jump-next-custom-icon:after,.rc-pagination-jump-prev-custom-icon:after {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  transition: all .2s;
  content: "\2022\2022\2022";
  opacity: 1;
  display: block;
  letter-spacing: 2px;
  color: #ccc;
  font-size: 12px;
  margin: 1px auto auto
}

.rc-pagination-jump-next-custom-icon .custom-icon-jump-next,.rc-pagination-jump-next-custom-icon .custom-icon-jump-prev,.rc-pagination-jump-prev-custom-icon .custom-icon-jump-next,.rc-pagination-jump-prev-custom-icon .custom-icon-jump-prev {
  opacity: 0;
  transition: all .2s
}

.rc-pagination-jump-next-custom-icon:hover:after,.rc-pagination-jump-prev-custom-icon:hover:after {
  opacity: 0;
  color: #ccc
}

.rc-pagination-jump-next-custom-icon:hover .custom-icon-jump-next,.rc-pagination-jump-next-custom-icon:hover .custom-icon-jump-prev,.rc-pagination-jump-prev-custom-icon:hover .custom-icon-jump-next,.rc-pagination-jump-prev-custom-icon:hover .custom-icon-jump-prev {
  opacity: 1;
  color: #2db7f5
}

.rc-pagination-jump-next,.rc-pagination-jump-prev,.rc-pagination-prev {
  margin-right: 8px
}

.rc-pagination-jump-next,.rc-pagination-jump-prev,.rc-pagination-next,.rc-pagination-prev {
  cursor: pointer;
  color: #666;
  font-size: 10px;
  border-radius: 6px;
  list-style: none;
  min-width: 28px;
  height: 28px;
  line-height: 28px;
  float: left;
  text-align: center
}

.rc-pagination-prev a:after {
  content: "\2039";
  display: block
}

.rc-pagination-next a:after {
  content: "\203A";
  display: block
}

.rc-pagination-next,.rc-pagination-prev {
  border: 1px solid #d9d9d9;
  font-size: 18px
}

.rc-pagination-next a,.rc-pagination-prev a {
  color: #666
}

.rc-pagination-next a:after,.rc-pagination-prev a:after {
  margin-top: -1px
}

.rc-pagination-disabled {
  cursor: not-allowed
}

.rc-pagination-disabled a {
  color: #ccc
}

.rc-pagination-options {
  float: left;
  margin-left: 15px
}

.rc-pagination-options-size-changer {
  float: left;
  width: 80px
}

.rc-pagination-options-quick-jumper {
  float: left;
  margin-left: 16px;
  height: 28px;
  line-height: 28px
}

.rc-pagination-options-quick-jumper input {
  margin: 0 8px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 6px;
  border: 1px solid #d9d9d9;
  outline: none;
  padding: 3px 12px;
  width: 50px;
  height: 28px
}

.rc-pagination-options-quick-jumper input:hover {
  border-color: #2db7f5
}

.rc-pagination-options-quick-jumper button {
  display: inline-block;
  margin: 0 8px;
  font-weight: 500;
  text-align: center;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  white-space: nowrap;
  padding: 0 15px;
  font-size: 12px;
  border-radius: 6px;
  height: 28px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: all .3s cubic-bezier(.645,.045,.355,1);
  position: relative;
  color: rgba(0,0,0,.65);
  background-color: #fff;
  border: 1px solid #d9d9d9
}

.rc-pagination-options-quick-jumper button:active,.rc-pagination-options-quick-jumper button:focus,.rc-pagination-options-quick-jumper button:hover {
  color: #2db7f5;
  background-color: #fff;
  border-color: #2db7f5
}

.rc-pagination-simple .rc-pagination-next,.rc-pagination-simple .rc-pagination-prev {
  border: none;
  height: 24px;
  line-height: 24px;
  margin: 0;
  font-size: 18px
}

.rc-pagination-simple .rc-pagination-simple-pager {
  float: left;
  margin-right: 8px;
  list-style: none
}

.rc-pagination-simple .rc-pagination-simple-pager .rc-pagination-slash {
  margin: 0 10px
}

.rc-pagination-simple .rc-pagination-simple-pager input {
  margin: 0 8px;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 6px;
  border: 1px solid #d9d9d9;
  outline: none;
  padding: 5px 8px;
  min-height: 20px
}

.rc-pagination-simple .rc-pagination-simple-pager input:hover {
  border-color: #2db7f5
}

.rc-pagination-simple .rc-pagination-simple-pager button {
  display: inline-block;
  margin: 0 8px;
  font-weight: 500;
  text-align: center;
  touch-action: manipulation;
  cursor: pointer;
  background-image: none;
  white-space: nowrap;
  padding: 0 8px;
  font-size: 12px;
  border-radius: 6px;
  height: 26px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  transition: all .3s cubic-bezier(.645,.045,.355,1);
  position: relative;
  color: rgba(0,0,0,.65);
  background-color: #fff;
  border: 1px solid #d9d9d9
}

.rc-pagination-simple .rc-pagination-simple-pager button:active,.rc-pagination-simple .rc-pagination-simple-pager button:focus,.rc-pagination-simple .rc-pagination-simple-pager button:hover {
  color: #2db7f5;
  background-color: #fff;
  border-color: #2db7f5
}

@media only screen and (max-width: 1024px) {
  .rc-pagination-item-after-jump-prev,.rc-pagination-item-before-jump-next {
      display:none
  }
}
